<script>
import moment from 'moment'
import SectionCloud from './SectionCloud.vue';
import SectionDevops from './SectionDevops.vue';
import SectionFooter from './SectionFooter.vue';
import SectionTechStack from './SectionTechStack.vue';
import SectionAchievements from './SectionAchievements.vue';

export default {
    components: {
        SectionCloud,
        SectionDevops,
        SectionFooter,
        SectionTechStack,
        SectionAchievements
    },
    data() {
        return {
            repoCount: ""
        }
    },
    mounted () {
        this.axios.get('https://cmoine.netlify.app/.netlify/functions/github-wrapper')
            .then((response) => {
                this.repoCount=response.data.filter(it => it.owner.login=='cmoine').length
            })
    },
    methods: {
        age(dob) {
            return this.getYears(this.getDuration(dob))
        },
        getDuration(d) {
            return moment.duration(moment(new Date()).diff(moment(d)))
        },
        getYears(d) {
            return Math.floor(d.asYears())
        },
        getMonths(d) {
            return Math.floor(d.asMonths() - this.getYears(d) * 12)
        },
        yxp() {
            var d=this.getDuration([2004, 9, 1])
            var y=this.getYears(d)
            var m=this.getMonths(d)
            var result=y + ' ' + this.$t('years') + ' ' + this.$tc('months', m, {count: m})
            return result;
        },
        doScroll() {
            this.$refs.sectionCloudChild.$refs.showMore.scrollIntoView({behavior: "smooth"})
        }
    },
    computed: {
        summary() {
            return this.$t('summary', {age : this.age([1980,8,2]), son: this.age([2014,1,17]), twins: this.age([2018,2,5])})
        }
    }
};
</script>
