import Vue from "vue";
import Router from "vue-router";
import AppHeader from "./layout/AppHeader";
import Profile from "./views/Profile.vue";
import Taxes from "./views/Taxes.vue";

Vue.use(Router);

export default new Router({
  linkExactActiveClass: "active",
  routes: [
    {
      path: "/",
      name: "profile",
      components: {
        header: AppHeader,
        default: Profile
      }
    },
    {
      path: "/taxes",
      name: "taxes",
      components: {
        default: Taxes
      }
    },
    {
      path: "/inactive",
      name: "inactive",
      components: {
        default: Inactive
      }
    },
    {
      path: "/confirmation_token",
      name: "confirmation_token",
      components: {
        default: Inactive
      }
    },
  ],
  scrollBehavior: to => {
    if (to.hash) {
      return { selector: to.hash };
    } else {
      return { x: 0, y: 0 };
    }
  }
});
